@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100;300;400;500;700;900&display=swap);
html,
body {
  background: rgb(0, 92, 184);
  background: linear-gradient(
      135deg,
      rgb(0, 92, 184) 0%,
      rgba(0, 45, 98, 1) 50%,
      rgba(0, 13, 28, 1) 100%
    )
    fixed;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0;
  padding: 0;
}

.App {
  color: #fff;
  text-align: center;
}

.Home {
  text-align: left;
}

.UkanaHeader,
.UkanaHeaderJp {
  display: inline-block;
  font-size: 5rem;
  font-weight: 500;
  margin: 0;
  margin-left: 1rem;
}

.UkanaHeaderJp {
  color: #bf1919;
  font-weight: 300;
}

.Subheader {
  margin: 0;
  margin-left: 1rem;
  font-style: italic;
}

.SubheaderStart {
  font-weight: 500;
  margin-left: 1rem;
}

.Navbar {
  background: #090645;
  padding: 0;
  text-align: left;
  width: auto;
}

a {
  color: #fff;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  display: inline-block;
  list-style-type: none;
  margin-right: 2rem;
  text-decoration: none;
}

.ukanaLogo {
  margin: 1rem 0 0.4rem 1rem;
  width: 2rem;
}

.navLink {
  margin: 0;
}

.non-home {
  position: relative;
  bottom: 1rem;
  font-size: 1.2rem;
}

.gradedKanji {
  color: rgb(174, 152, 152);
  font-style: italic;
}

.numbered {
  border: solid 3px #fff;
  border-radius: 50%;
  height: 30px;
  text-align: center;
  width: 30px;
}

.numbered:hover {
  -webkit-animation: numberHover 0.5s forwards;
          animation: numberHover 0.5s forwards;
}

@-webkit-keyframes numberHover {
  from {
    background: #090645;
  }
  to {
    background: #346adf;
  }
}

@keyframes numberHover {
  from {
    background: #090645;
  }
  to {
    background: #346adf;
  }
}

.wordSearchInput {
  border: solid 0px;
  border-radius: 25px;
  font-family: "Noto Sans JP", sans-serif;
  height: 1.5rem;
  text-align: center;
}

.KanjiCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: auto;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiCardKanji {
  font-size: 5rem;
  margin: 0;
}

.KanjiCardMeaning {
  margin: 0;
}

.refreshRandom {
  background-color: #bf1919;
  border: solid 3px #bf1919;
  border-radius: 25px;
  color: #fff;
  cursor: pointer;
  font-size: 2rem;
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiMiniCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  display: inline-block;
  margin: 0 0.5rem;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: left;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.KanjiCardHeader {
  text-align: center;
}

.KanjiMiniCardKanji {
  font-size: 3rem;
  margin: 0;
}

.Hiragana,
.Katakana {
  display: flex;
  flex-flow: row wrap;
}

.KanaMiniCard {
  background: #fff;
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 0 0 16%;
}

.emptyCard {
  background: rgba(255, 255, 255, 0);
  border-radius: 20px;
  color: #000;
  margin: 0 1%;
  margin-top: 1rem;
  padding: 0.5rem;
  text-align: center;
  flex: 0 0 16%;
}

.KanaMiniCardKana,
.KanaMiniCardRomaji {
  font-size: 3.5vw;
  margin: 0;
  padding: 0;
}

